<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process288"></a>

<p><br></p>
<p><span class="rvts17">塞碧莉（</span><span class="rvts87">Sebille</span><span class="rvts17">）：</span></p>
<p class="rvps2"><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1551.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts81">故事背景：</span>
<br><span class="rvts21">塞碧莉是名出色的刺客杀手，她的原主人在她脸上留下了一道伤疤，并把她当做奴隶看待。她的原主人通过一首可以控制塞碧莉的歌，命令她杀掉了自己的族人。后来，塞碧莉有幸逃出了原主人的控制，她接下来要做的事情就是找到控制她的那个原主人，并进行复仇。</span>
<br><span class="rvts20"></span>
<br><span class="rvts81">支线任务：</span>
<br><span class="rvts21">欢乐堡部分：</span>
<br><span class="rvts20">1. </span><span class="rvts21">在欢乐堡外围的厨房区域，当开始</span><span class="rvts20"></span><span class="rvts21">一个被囚禁的精灵</span><span class="rvts20"></span><span class="rvts21">支线时，塞碧莉会找到之后在海滩休息的斯汀泰尔（</span><span class="rvts20">Stingtail</span><span class="rvts21">），如果让塞碧莉与他谈话，他会杀掉斯汀泰尔（因为斯汀泰尔偷了格里夫的橘子），之后让她与格里夫（</span><span class="rvts20">Griff</span><span class="rvts21">）谈话。需要注意的是，一定不要揭发斯汀泰尔是小偷，不然他会被刺客杀掉。另外一定要让塞碧莉与格里夫谈话，才能引出接下来的内容。</span>
<br><span class="rvts20">2. </span><span class="rvts21">格里夫会告诉她去找空洞沼泽里的祖尔斯卡（</span><span class="rvts20">Zaleskar</span><span class="rvts21">），祖尔斯卡会告诉你接下来要做的事情。（必须首先劝说成功祖尔斯卡，不然他会攻击你）</span>
<br><span class="rvts21">死神海岸部分：</span>
<br><span class="rvts20">3. </span><span class="rvts21">在死神海岸的精灵营地，说服精灵进入仪式，去救被困在锯木厂的萨黑拉（前提是萨黑拉在欢乐堡没有被你杀掉）。成功解救萨黑拉后，她会告诉塞碧莉的真实身份以及塞碧莉前主人就是无名岛的暗影王子。</span>
<br><span class="rvts21">无名岛部分：</span>
<br><span class="rvts20">4. </span><span class="rvts21">来到无名岛后，塞碧莉会教给我们其他人一首歌，使用这首歌可以打破暗影王子对她的束缚。接着，找到蜥蜴神殿废墟处的暗影王子并杀掉他，塞碧莉将得到复仇。注意，在这场战斗中，暗影王子试图使用独有的歌曲控制塞碧莉，并且暗影王子拥有几个强悍的暗影刺客，使用塞碧莉之前教给我们的歌可以破除她被控制的魔法。</span>
<br><span class="rvts20">5. </span><span class="rvts21">回到无名岛的精灵神殿，精灵母树会和塞碧莉谈话，并想让她成为下一任精灵母树。同意母树的要求，母树将赐予她</span><span class="rvts20"></span><span class="rvts21">扎根（</span><span class="rvts20">Rooted</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">天赋。</span>
<br><span class="rvts20">6. </span><span class="rvts21">当要离开母树的时候，萨黑拉会过来并建议杀掉母树以释放所有受折磨的精灵族人，这样做将导致整个精灵神殿的人的敌对，但塞碧莉的支线任务将因此圆满终结。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



